html {
  scroll-behavior: smooth;
}

a {
  color: inherit;
}

.noUnderline {
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
}

.underline {
  text-underline-offset: 3px;
  text-decoration: underline;
}

.pageTitleDiv {
  flex-wrap: wrap;
  width: 100%;
  height: 75vh;
  display: inline-flex;
  position: relative;
}

.flex-direction {
  flex-direction: row;
}

.titleFlexL {
  float: left;
  width: 70%;
  height: 100%;
  display: flex;
  position: relative;
}

.bgCircle {
  z-index: 1;
  background-image: linear-gradient(90deg, #7a7a7a, #000);
  border-radius: 50%;
  width: calc(18vw + 35px);
  height: calc(18vw + 35px);
  position: absolute;
  top: calc(50% - 8.5vw - 17.5px);
  left: 5%;
}

.title {
  color: #fd5151;
  z-index: 2;
  align-self: center;
  margin: 0%;
  padding: 0%;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 8vw;
  font-weight: 700;
  position: relative;
  left: 20%;
}

.titleFlexR {
  float: right;
  width: 30%;
  position: relative;
}

.jumpTitle {
  text-align: center;
  color: silver;
  text-underline-offset: 2px;
  margin-bottom: 5px;
  font-size: 3vw;
  font-weight: 300;
  text-decoration: underline 4px #fd5151;
  position: relative;
}

.jumpBox {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100% - 3vw);
  display: flex;
  position: relative;
  top: 60px;
}

.jumpItem {
  width: 100%;
  height: 25%;
  position: relative;
}

.jumpItem:hover .jumpCircle {
  width: calc(18.75vh - 20px - .75vw);
  height: calc(18.75vh - 20px - .75vw);
  top: 0%;
  left: calc(50% - 9.375vh + 9.375px + .375vw);
}

.jumpItem:hover .jumpText {
  font-size: 4vw;
  top: calc(50% - 2vw - 12px);
}

.jumpCircle {
  cursor: default;
  background-color: #fd5151;
  border-radius: 100%;
  width: 0;
  height: 0;
  transition: width .5s, height .5s, top .5s, left .5s;
  position: absolute;
  top: 50%;
  left: 50%;
}

.jumpText {
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: 3vw;
  transition: font-size .5s, top .5s;
  position: relative;
  top: calc(50% - 1.5vw - 5px);
}

.jumpLink {
  color: silver;
  text-decoration: none;
}

@media only screen and (width <= 768px) {
  .bgCircle {
    width: calc(18vh + 35px);
    height: calc(18vh + 35px);
    position: absolute;
    top: calc(50% - 9vh - 17.5px);
  }

  .pageTitleDiv {
    flex-direction: column;
    height: 80vh;
  }

  .titleFlexL {
    width: 100%;
    height: 40vh;
  }

  .title {
    font-size: 12vw;
    left: 20%;
  }

  .jumpBox {
    top: 0;
  }

  .titleFlexR {
    width: 100%;
    height: 40vh;
  }

  .jumpTitle {
    margin-top: 0%;
    font-size: 5vw;
    text-decoration-thickness: 4px;
  }

  .jumpItem:hover .jumpCircle {
    width: 0;
    height: 0;
  }

  .jumpText {
    font-size: 5vw;
    top: calc(50% - 2.5vw);
  }

  .jumpItem:hover .jumpText {
    font-size: 6vw;
    top: calc(50% - 3vw);
  }
}

.pageSection {
  width: 100%;
  margin-top: 50px;
  margin-bottom: 50px;
  position: relative;
}

.sectionTitle {
  text-align: center;
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 4.5vw;
  font-weight: 300;
  overflow: hidden;
}

.sectionTitle:before, .sectionTitle:after {
  content: "";
  vertical-align: middle;
  background-color: #fd5151;
  border-radius: 2px;
  width: 50%;
  height: 6px;
  display: inline-block;
  position: relative;
}

.sectionTitle:before {
  margin-left: -50%;
  right: .5em;
}

.sectionTitle:after {
  margin-right: -50%;
  left: .5em;
}

.cardContainer {
  width: 80%;
  height: fit-content;
  margin-top: 10vh;
  position: relative;
  left: 10%;
}

.card {
  background: #282828;
  border: 2.5px solid #1e1e1e;
  border-radius: 15px;
  height: -moz-fit-content;
  margin: 10px;
  padding: 30px 40px;
  transition: all .5s;
  position: relative;
}

.card:hover {
  transform: scale(1.03);
}

.subsectionTitle {
  color: #fff;
  text-underline-offset: 3px;
  font-size: 2vw;
  text-decoration: underline 3px #fd5151;
}

.sectionDuration {
  color: #979797;
  font-size: calc(.75vw + 6px);
}

.sectionBody {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 1.3vw;
  position: relative;
}

.tab {
  padding-left: 4ch;
}

.toolImg {
  float: right;
  width: auto;
  height: 2vw;
  margin-left: 1vw;
}

.projectLink {
  float: none;
  vertical-align: middle;
  filter: invert();
  border-radius: 5px;
  width: auto;
  height: 3vw;
  margin-left: 1vw;
}

.projectLink:hover {
  background-color: #a7a7a7;
}

@media only screen and (width <= 768px) {
  .pageSection {
    margin-top: 0;
    margin-bottom: 40px;
  }

  .sectionTitle {
    font-size: 4.5vh;
  }

  .sectionTitle:before, .sectionTitle:after {
    height: 1vh;
  }

  .cardContainer {
    width: 96%;
    margin-top: 6vh;
    left: 2%;
  }

  .card {
    padding: 20px;
  }

  .card:hover {
    transform: none;
  }

  .subsectionTitle {
    color: #fff;
    text-underline-offset: 3px;
    font-size: 3vh;
    line-height: 4vh;
    text-decoration: underline 2px #fd5151;
  }

  .sectionDuration {
    font-size: 1.8vh;
    line-height: 2vh;
  }

  .sectionBody {
    color: #fff;
    font-family: Roboto, sans-serif;
    font-size: 2.2vh;
    position: relative;
  }

  .toolImg {
    height: 3vh;
  }

  .projectLink {
    height: 4vh;
  }
}

.pageAbs {
  position: absolute;
  left: 0;
  right: 0;
}

.skills, .education {
  width: 100%;
  height: fit-content;
  padding-bottom: 25px;
  position: relative;
}
